<template>
  <el-cascader
    popper-class="CascaderTree"
    ref="cascader"
    v-model="Value"
    :placeholder="placeholder"
    :options="options"
    :props="oprops"
    :show-all-levels="ShowALLLevels"
    clearable
    :collapse-tags="CollapseTags"
    :size="size"
    @change="ChangeHandle"
  >
    <template slot-scope="{ node, data }">
      <i
        class="iconfont icon_smart_terminal"
        v-if="node.level === 4 && data.DeviceType === 1"
        >智能终端</i
      >
      <i
        class="iconfont icon_pc"
        v-else-if="node.level === 4 && data.DeviceType === 2"
        >PC</i
      >
      <i
        class="iconfont icon_IPPhone"
        v-else-if="node.level === 4 && data.DeviceType === 3"
        >IP电话</i
      >
      <i
        class="iconfont icon_Camera"
        v-else-if="node.level === 4 && data.DeviceType === 4"
        >{{ data.CameraSite === "2" ? "老师" : "学生" }}摄像头</i
      >
      <i
        class="iconfont icon_VideoCard"
        v-else-if="node.level === 4 && data.DeviceType === 5"
        >课件编码器</i
      >
      <i
        class="iconfont icon_icCardReader"
        v-else-if="node.level === 4 && data.DeviceType === 7"
        >IC卡读卡器</i
      >
      <i
        class="iconfont icon_IOT"
        v-else-if="node.level === 4 && data.DeviceType === 8"
        >{{ data.Name }}</i
      >
      <i
        class="iconfont icon_BroadDevice"
        v-else-if="node.level === 4 && data.DeviceType === 9"
        >录播主机</i
      >
      <i
        class="iconfont icon_WDSMIC"
        v-else-if="node.level === 4 && data.DeviceType === 10"
      />
      <i
        class="iconfont icon_Projector"
        v-else-if="node.level === 4 && data.DeviceType === 11"
        >投影仪</i
      >
      <i
        class="iconfont icon_guard"
        v-else-if="node.level === 4 && data.DeviceType === 12"
      />
      <i
        class="iconfont icon_power"
        v-else-if="node.level === 4 && data.DeviceType === 13"
      />
      <i
        class="iconfont icon_Speaker"
        v-else-if="node.level === 4 && data.DeviceType === 14"
      />
      <i
        class="iconfont icon_Geeklink"
        v-else-if="node.level === 4 && data.DeviceType === 20"
      />
      <i
        class="iconfont icon_Air"
        v-else-if="node.level === 4 && data.DeviceType === 21"
      />
      <i
        class="iconfont icon_Fan"
        v-else-if="node.level === 4 && data.DeviceType === 22"
      />
      <i
        class="iconfont icon_curtains"
        v-else-if="node.level === 4 && data.DeviceType === 23"
      />
      <i
        class="iconfont icon_mall-switch"
        v-else-if="node.level === 4 && data.DeviceType === 24"
      />
      <i
        class="iconfont icon_scene1"
        v-else-if="node.level === 4 && data.DeviceType === 30"
      />
      <i
        class="iconfont icon_gateway"
        v-else-if="node.level === 4 && data.DeviceType === 40"
      />
      <i
        class="iconfont icon_switch1"
        v-else-if="node.level === 4 && data.DeviceType === 41"
      />
      <i
        class="iconfont icon_switch2"
        v-else-if="node.level === 4 && data.DeviceType === 42"
      />
      <i
        class="iconfont icon_switch3"
        v-else-if="node.level === 4 && data.DeviceType === 43"
      />
      <i
        class="iconfont icon_switch_L"
        v-else-if="node.level === 4 && data.DeviceType === 44"
      />
      <i
        class="iconfont icon_InfraRed"
        v-else-if="node.level === 4 && data.DeviceType === 45"
      />
      <i
        class="iconfont icon_Fan"
        v-else-if="node.level === 4 && data.DeviceType === 46"
      />
      <i
        class="iconfont icon_curtains"
        v-else-if="node.level === 4 && data.DeviceType === 47"
      />
      <i
        class="iconfont icon_InfraRed"
        v-else-if="node.level === 4 && data.DeviceType === 48"
      />
      <span>{{ data.Name }}</span>
      <!-- <span v-if="node.level < 4">{{ data.Name }}</span> -->
      <label v-if="data.disabled">{{ DisabledText }}</label>
    </template>
  </el-cascader>
</template>

<script>
import api from "@/scripts/api";
let count = 0;
export default {
  props: {
    // 禁止项 编号
    DisabledItems: {
      type: Array,
      default: () => []
    },
    // 禁止第几级
    DisabledLevel: {
      type: Number,
      default: 3
    },
    // 禁止的提示信息
    DisabledText: { type: String, default: "已排课" },
    // 输入框中是否显示选中值的完整路径
    ShowALLLevels: {
      type: Boolean,
      default: false
    },
    size: {
      type: String,
      default: "mini"
    },
    value: {},
    placeholder: {
      type: String,
      default: "请选择"
    },
    Values: {
      type: Array,
      default: () => [],
    },
    /**
     * 教室编号，用于主动加载这间教室的设备 Level 为4时有效
     */
    ClsRoomID: {
      type: String,
      default: ""
    },
    /**
     * 排除课表
     */
    ExCSID: {
      type: String,
      default: ""
    },
    /**
     * 排课日期
     */
    ScheduleDate: {},
    /**
     * 排课节次
     */
    ScheduleLesson: {},
    /**
     * 显示级别：
     * 1: 校区
     * 2: 建筑物
     * 3: 教室
     */
    Level: {
      type: Number,
      default: 1
    },
    /**
     * 选择任意一级选项
     */
    checkStrictly: {
      type: Boolean,
      default: false
    },
    /**
     * 折叠
     */
    CollapseTags: {
      type: Boolean,
      default: true
    },
    /**
     * 多选
     */
    multiple: {
      type: Boolean,
      default: false
    },
    // 录播教室
    IsRBClsRoom: {
      type: Boolean,
      default: false
    },
    // RJRHx终端教室
    FJRHClsRoom: {
      type: Boolean,
      default: false
    },
  },
  watch: {
    FJRHClsRoom (newVal, oldVal) {
      this.LoadData();
    },
    value (newVal, oldVal) {
      this.Value = newVal;
      count = count + 1;
      if (count > 1 && newVal) {
        this.options.forEach((a) => {
          if (a.options && a.options.length > 0) {
            for (var b of a.options) {
              if (b.options && b.options.length > 0) {
                for (var c of b.options) {
                  if (this.ClsRoomID === c.ID && this.Level === 4) {
                    this.LoadDevice(this.ClsRoomID, c);
                  }
                }
              }
            }
          }
        })
      }
    },
    ScheduleDate (newVal, oldVal) {
      if (newVal !== oldVal) this.ReVerify();
    },
    ScheduleLesson (newVal, oldVal) {
      if (newVal !== oldVal) this.ReVerify();
    },
    ClsRoomID (val) {
      count = count + 1;
    },
    DisabledItems (newVal, oldVal) {
      this.$nextTick(() => {
        this.ReDisabled(this.options);
      })
    }
  },
  data () {
    const $this = this;
    return {
      Value: this.value,
      options: [],
      oprops: {
        multiple: this.multiple,
        checkStrictly: this.checkStrictly,
        emitPath: false,
        value: "ID",
        id: "ID",
        label: "Name",
        children: "options",
        expandTrigger: "hover",
        lazy: this.Level === 4,
        async lazyLoad (node, resolve) {
          if (node.level > 0) {
            // 加载第四级 设备
            if ($this.Level === 4 && node.level === 3) {
              if (node.options && node.options.length > 0) {
                resolve([]);
              } else {
                const res = await api.Device.GetDeviceByRoomID(node.data.ID); // 加载设备
                const datas = [];
                if (res) {
                  res.Datas.forEach((element) => {
                    datas.push({
                      Level: 4,
                      ID: element.DeviceID,
                      Name: element.DeviceName,
                      leaf: "leaf",
                      DeviceType: element.DeviceType,
                      CameraSite: element.Value.CameraSite,
                      options: null,
                    });
                  });

                  resolve(datas);
                }
              }
            } else {
              resolve([]);
            }
          }
        }
      }
    }
  },
  mounted () {
    this.LoadData();
  },
  methods: {
    // 重新设置禁止项
    ReDisabled (options) {
      if (options && options.length > 0) {
        options.forEach((item) => {
          if (item.Level === this.DisabledLevel) {
            item.disabled = this.DisabledItems.indexOf(item.ID) >= 0;
          } else {
            if (item.options && item.options.length > 0) {
              this.ReDisabled(item.options);
            }
          }
        })
      }
    },
    // 操作后响应
    ChangeHandle (val) {
      const checkNode = this.$refs.cascader.getCheckedNodes()[0];
      if (this.multiple || (checkNode && checkNode.level === this.Level)) {
        this.$emit("update:value", this.Value);

        const checkedNodes = this.$refs.cascader.getCheckedNodes();
        if (checkedNodes && checkedNodes.length > 0) {
          this.$emit(
            "update:Values",
            this.$refs.cascader.getCheckedNodes()[0].path
          )
          this.$emit(
            "update:name",
            this.$refs.cascader.getCheckedNodes()[0].label
          );
        } else {
          this.$emit("update:Values", []);
        }

        const LZString = require("lz-string");
        const jsonStr = JSON.stringify(val);
        let result = "";
        if (jsonStr) {
          result = LZString.compressToBase64(
            jsonStr.slice(1, jsonStr.length - 1)
          )
        }

        this.$emit("Filter", result);
      } else {
        this.Value = [];
        this.$emit("update:value", this.Value);
        this.$emit("update:Values", []);
        this.$emit("update:name", "");
      }
    },
    /**
     * 加载数据
     */
    async LoadData () {
      this.options = [];
      const resC = await api.Campuses.GetAllDatas(); // 加载校区

      if (resC) {
        await resC.Datas.forEach((element) => {
          this.options.push({
            Level: 1,
            ID: element.CampusesID,
            Name: element.CampusesName,
            options: this.Level === 1 ? null : [],
          })
        })

        if (this.Level > 1) {
          const resB = await api.Building.GetAllDatasByCampuses(); // 加载建筑物
          if (resB) {
            await resB.Datas.forEach((element) => {
              const findC = this.options.find(
                (c) => c.ID === element.CampusesID
              );
              if (findC) {
                findC.options.push({
                  Level: 2,
                  ID: element.BuildingID,
                  Name: element.BuildingName,
                  options: this.Level === 2 ? null : [],
                })
              }
            })
            if (this.Level > 2) {
              let resR = [];
              if (this.IsRBClsRoom) {
                resR = await api.RBConfig.GetAllRooms(); // 加载教室
              } else {
                resR = await api.ClsRoom.GetDatasBySchool(this.FJRHClsRoom); // 加载教室
              }

              const resSck = [];
              if (this.ScheduleDate && this.ScheduleLesson) {
                const recSR = await api.ClsRoom.GetScheduleClsRooms(
                  this.ScheduleDate,
                  this.ScheduleLesson
                )

                if (recSR) {
                  recSR.Datas.forEach((c) => {
                    resSck.push(c);
                  })
                }
              }

              if (resR) {
                await resR.Datas.forEach(async (element) => {
                  const findC = this.options.find(
                    (c) => c.ID === element.CampusesID
                  )

                  if (findC && findC.options) {
                    const findB = findC.options.find(
                      (b) => b.ID === element.BuildingID
                    )

                    if (findB) {
                      let disabled = false;
                      if (resSck) {
                        const findSCK = resSck.find(
                          (c) => c.ClsRoomID === element.ClsRoomID
                        )
                        if (findSCK) {
                          if (this.ExCSID !== findSCK.CSID) {
                            disabled = true;
                          }
                        }
                      }

                      if (this.DisabledLevel === 3 && this.DisabledItems) {
                        disabled =
                          this.DisabledItems.indexOf(element.ClsRoomID) >= 0;
                      }

                      // 如果第4级 且 有设置教室编号的则主动加载这间教室的设备
                      const devices = [];
                      if (
                        this.Level === 4 &&
                        this.ClsRoomID === element.ClsRoomID
                      ) {
                        const res = await api.Device.GetDeviceByRoomID(
                          element.ClsRoomID
                        );
                        if (res) {
                          res.Datas.forEach((d) => {
                            devices.push({
                              Level: 4,
                              ID: d.DeviceID,
                              Name: d.DeviceName,
                              leaf: "leaf",
                              DeviceType: d.DeviceType,
                              CameraSite: d.Value.CameraSite,
                              options: null,
                            })
                          })
                        }
                      }

                      findB.options.push({
                        Level: 3,
                        ID: element.ClsRoomID,
                        Name: element.ClsRoomName,
                        options: this.Level === 3 ? null : devices,
                        disabled: disabled,
                      })
                    }
                  }
                })
              }
            }
          }
        }
      }

      this.RemoveEmpty(this.options);
    },
    async LoadDevice (id, c) {
      const res = await api.Device.GetDeviceByRoomID(id);
      const arr = [];
      if (res) {
        res.Datas.forEach((d) => {
          arr.push({
            Level: 4,
            ID: d.DeviceID,
            Name: d.DeviceName,
            leaf: "leaf",
            DeviceType: d.DeviceType,
            CameraSite: d.Value.CameraSite,
            options: null,
          })
        })
      }
      c.options = arr;
    },
    /**
     * 重新验证已选中的值
     */
    async ReVerify () {
      const resSck = await api.ClsRoom.GetScheduleClsRooms(
        this.ScheduleDate,
        this.ScheduleLesson
      )

      if (resSck) {
        this.options.forEach((c) => {
          if (c.options) {
            c.options.forEach((b) => {
              if (b.options) {
                b.options.forEach((r) => {
                  r.disabled = false;
                })
              }
            })
          }
        })

        resSck.Datas.forEach((element) => {
          const findC = this.options.find((c) => c.ID === element.CampusesID);
          if (findC) {
            const findB = findC.options.find(
              (b) => b.ID === element.BuildingID
            )
            if (findB) {
              const findR = findB.options.find(
                (b) => b.ID === element.ClsRoomID
              )
              if (findR) {
                if (element.CSID !== this.ExCSID) {
                  findR.disabled = true;
                  if (this.multiple) {
                    this.Value.remove(element.ClsRoomID);
                  } else {
                    if (this.Value === element.ClsRoomID) this.Value = "";
                  }
                  this.$emit("update:value", this.Value);
                }
              }
            }
          }
        })
      }
    },
    /**
     * 移除空项
     */
    async RemoveEmpty (options) {
      if (options) {
        for (let i = options.length - 1; i >= 0; i--) {
          if (options[i].Level < (this.Level === 4 ? 3 : this.Level)) {
            if (!(options[i].options && options[i].options.length > 0)) {
              options.splice(i, 1);
            } else {
              this.RemoveEmpty(options[i].options);
            }
          }
        }
      }
    }
  }
};
</script>

<style lang="less">
.CascaderTree {
  .el-cascader-panel {
    .el-cascader-menu {
      .el-scrollbar__wrap {
        overflow: auto;
        height: 250px;
      }
      .el-cascader-node {
        .el-cascader-node__label {
          > * {
            float: left;
          }

          i {
            margin-right: 5px;
            font-size: 14px;
          }

          i::before {
            margin-right: 5px;
          }

          label {
            margin-left: 8px;
            .disabled_red();
          }
        }
      }
    }
  }

  .is-disabled {
    * {
      cursor: not-allowed;
    }
  }
}
</style>
