<template>
  <div id="file-upload">
    <el-upload
      action
      :file-list="file"
      :on-change="UploadChange"
      :on-remove="RemoveHandle"
      :accept="fileType"
      :auto-upload="false"
      :list-type="type"
      v-loading="UploadLoding"
    >
      <el-button type="primary" icon="el-icon-plus" slot="trigger" size="small"
        >文件上传</el-button
      >
      <div slot="tip" class="el-upload__tip">
        {{ slotTip }}
      </div>
    </el-upload>
  </div>
</template>
<script>
import api from "@/scripts/api";
import { uuid } from "@/scripts/utils";
export default {
  props: {
    // 文件类型
    fileType: {
      type: String,
      default: ""
    },
    slotTip: {
      type: String,
      default: ""
    },
    // 文件列表的类型
    type: {
      type: String,
      default: "text"
    },
    fileList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      file: [],
      UploadLoding: false
    };
  },
  watch: {
    fileList(val) {
      this.file = val;
    }
  },
  methods: {
    /**
     * 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
     */
    UploadChange(file, fileList) {
      this.file = fileList;
    },
    /**
     * 文件列表移除文件时的钩子
     */
    RemoveHandle(file, fileList) {
      this.file = fileList;
    },
    /**
     * 上传 other除stream，fileTyp的额外参数
     */
    async Upload(type, other) {
      let pic = "";
      if (this.file && this.file.length > 0) {
        this.UploadLoding = true;
        for (const f of this.file) {
          if (f.status === "ready") {
            const fd = new FormData();
            const uid = uuid();
            fd.append("stream", f.raw);
            fd.append("fileType", type);
            if (other) {
              for (var o of other) {
                fd.append(o.field, uid);
              }
            }
            const res = await api.Common.UploadFile(fd);
            if (res) {
              pic += res.Data.split(".")[0] + ",";
            }
          } else {
            pic += f.id + ",";
          }
        }
        if (pic) {
          this.UploadLoding = false;
          return pic.slice(0, -1);
        }
      }
    }
  }
};
</script>
<style lang="less"></style>
