<template>
  <div class="sub_module">
    <div class="module_header">
      <div class="title">学生提问</div>
      <div class="more" v-if="studentQuestion.length>0" @click="dialogVisible=true">
        查看详情
        <i class="el-icon-arrow-right"></i>
      </div>
    </div>

    <el-row :gutter="20" v-loading="listLoading">
      <el-col :span="6" v-for="(item, index) in scorePreview" :key="index">
        <el-card shadow="hover" class="list_card blue_bg">
          <el-row :gutter="20">
            <el-col :span="6" class="p-info">
<!--              <el-avatar size="large" shape="square" :src="DefaultHeadURL"></el-avatar>-->
              <img src="" class="avatar" v-real-img="[item['UserID'], item['StudentID']]" alt/>
              <div class="name">{{item.StudentName}}</div>
            </el-col>
            <el-col :span="18" class="card_info">
              <div class="card_line" v-for="(sub, k) in item['Questions']" :key="k">
                <div>{{FormatDate(sub['CreateTime'], 'hh:mm:ss')}}</div>
                <p>{{sub['QContent']}}</p>
              </div>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog title="学生提问记录" :visible.sync="dialogVisible" :close-on-click-modal="false" :modal-append-to-body="false">
      <el-row :gutter="20">
        <el-col :span="18">
          <!-- 提问记录 -->
          <div class="chat" ref="chat">
            <div v-if="dataList && dataList.length>0" class="chat_content">
              <div v-for="(sub, k) in dataList" :key="k"
                   :class=" sub.Key['StudentID']!== selectStudentId ? 'chat_item' : 'chat_item checked_item'">
                <div class="chat_news">
                  <div class="chat_news_name">
                    {{sub['StudentName']}}
                    <span>{{FormatDate(sub.Key['CreateTime'], 'hh:mm:ss')}}</span>
                  </div>
                  <div class="chat_news_cont">
                    <span>{{sub.Key['QContent'] }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="summary">提问人员</div>
          <div class="list_person">
            <div v-for="(item, index) in studentQuestion" :key="index" class="person_item"
                 :class="selectStudentId === item['StudentID'] ? 'person_selected' : ''"
                 @click="selectStudentId=item['StudentID']">
<!--              <el-avatar size="small" shape="square" :src="DefaultHeadURL"></el-avatar>-->
              <img src="" v-real-img="[item['UserID'], item['StudentID']]" alt/>
              <span>{{item['StudentName']}}</span>
              <span></span>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/scripts/api";
import { FormatDate, getSession } from "@/scripts/utils";

export default {
  name: "StudentQuestion",
  data () {
    return {
      courseRecord: getSession("campus_CourseRecord"),
      courseRecordID: getSession("campus_CourseRecordID"),
      dataList: [],
      studentQuestion: [],
      selectStudentId: "",
      listLoading: false,
      dialogVisible: false
    };
  },
  created () {
    this.getStudentQuestion();
  },
  methods: {
    FormatDate (dateValue, format) {
      return FormatDate(dateValue, format);
    },
    // 获取学生提问
    getStudentQuestion () {
      this.dataList = [];
      this.studentQuestion = [];
      this.listLoading = true;
      api.IQuestion.TeacherDataList(
        this.courseRecordID
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataList.push(item);
            const index = this.studentQuestion.findIndex(
              r => r.StudentID === item.Key["StudentID"]
            );
            if (index > -1) {
              this.studentQuestion[index].Questions.push(item.Key);
            } else {
              this.studentQuestion.push({
                Sno: item["sno"],
                StudentID: item.Key["StudentID"],
                UserID: item["UserID"],
                StudentName: item["StudentName"],
                Questions: [item.Key]
              });
            }
          });
        }
        this.listLoading = false;
      }).catch(() => {
        this.listLoading = false;
      });
    }
  },
  computed: {
    scorePreview () {
      return this.studentQuestion.filter((r, k) => k < 6);
    }
  }
};
</script>

