<template>
  <el-tooltip
    content="输入用户关键字可以进行模糊筛选"
    :placement="TooltipPlacement"
  >
    <el-select
      popper-class="selUsers"
      filterable
      :multiple="multiple"
      :multiple-limit="limit"
      v-model="Value"
      :placeholder="Placeholder"
      collapse-tags
      @change="ChangeHandle"
      remote
      clearable
      :remote-method="remoteMethod"
      reserve-keyword
      :loading="loading"
    >
      <el-option
        v-for="item in Datas"
        :key="item.UserID"
        :label="`${item.UserName} (${item.TeacherName}${item.StudentName})`"
        :value="item.UserID"
      >
        <span>{{ item.UserName }}</span>
        <span>{{ item.TeacherName }}{{ item.StudentName }}</span>
      </el-option>
    </el-select>
  </el-tooltip>
</template>

<script>
import api from "@/scripts/api";

export default {
  props: {
    /**
     * 多选
     */
    multiple: {
      type: Boolean,
      default: false
    },
    value: {},
    TooltipPlacement: {
      type: String,
      default: "right"
    },
    Placeholder: {
      type: String,
      default: "请输入关键词"
    },
    // 是否为多级
    casder: {
      type: Boolean,
      default: false
    },
    // 已选择的人员
    total: {
      type: Array,
      default: () => []
    },
    limit: {
      type: Number,
      default: 0
    }
  },
  watch: {
    async value(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.loading = true;
        const res = await api.User.GetUserByKey(
          "",
          this.multiple ? JSON.stringify(newVal) : `["${newVal}"]`
        );
        if (res) {
          this.Datas = res.Datas;
          this.Value = newVal;
        }
        this.loading = false;
      }
    }
  },
  data() {
    return { loading: false, Value: this.value, Datas: [] };
  },
  mounted() {
    // this.LoadData();
  },
  methods: {
    ChangeHandle() {
      this.$emit("update:value", this.Value);
      this.$emit("change");
    },
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        this.LoadData(query);
      } else {
        this.Datas = [];
      }
    },
    async LoadData(name) {
      const res = await api.User.GetUserByKey(
        name,
        this.multiple
          ? JSON.stringify(this.value)
          : `[${JSON.stringify(this.value)}]`
      );
      if (res) {
        this.Datas = res.Datas;
        if (this.casder) {
          const arr = [];
          res.Datas.forEach(ele => {
            const s = this.total.find(d => d === ele.UserID);
            if (!s) {
              arr.push(ele);
            }
          });
          this.Datas = arr;
        }
      }
      this.loading = false;
    }
  }
};
</script>

<style lang="less"></style>
