<template>
  <el-calendar ref="ScheduleCalendar" v-model="ChooseDate" class="schedule">
    <template slot="dateCell">
      <el-badge is-dot :hidden="ScheduledDate.indexOf(FormatDate(data.day)) < 0">
        <div class="dayItem">{{FormatDay(data.day)}}</div>
      </el-badge>
    </template>
  </el-calendar>
</template>

<script>
import moment from "moment";
export default {
  props: {
    ScheduledDate: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      ChooseDate: new Date(),
      Month: [
        moment(this.ChooseDate).format("Y-MM") + "-01",
        moment(this.ChooseDate).format("Y-MM") +
        "-" +
        moment(moment(this.ChooseDate).format("Y-MM"), "Y-MM").daysInMonth()
      ]
    };
  },
  watch: {
    Month(newVal, oldVal) {
      if (newVal[0] !== oldVal[0] && newVal[1] !== oldVal[1]) {
        this.$emit("MonthChanged", newVal);
      }
    },
    ChooseDate(newVal, oldVal) {
      this.Month = [
        moment(newVal).format("Y-MM") + "-01",
        moment(newVal).format("Y-MM") +
        "-" +
        moment(moment(newVal).format("Y-MM"), "Y-MM").daysInMonth()
      ];
      if (newVal !== oldVal) {
        this.$emit("Changed", newVal);
      }
      this.$emit("update:ChooseDate", newVal);
    }
  },
  mounted() {
    this.$nextTick(() => {
      // 点击前一个月
      const prevBtn = document.querySelector(
        ".el-calendar__button-group .el-button-group>button:nth-child(1)"
      );
      prevBtn.addEventListener("click", () => {
        this.Month = [
          moment(this.ChooseDate).format("Y-MM") + "-01",
          moment(this.ChooseDate).format("Y-MM") +
          "-" +
          moment(moment(this.ChooseDate).format("Y-MM"), "Y-MM").daysInMonth()
        ];
      });

      // 点击后一个月
      const nextBtn = document.querySelector(
        ".el-calendar__button-group .el-button-group>button:last-child"
      );
      nextBtn.addEventListener("click", () => {
        this.Month = [
          moment(this.ChooseDate).format("Y-MM") + "-01",
          moment(this.ChooseDate).format("Y-MM") +
          "-" +
          moment(moment(this.ChooseDate).format("Y-MM"), "Y-MM").daysInMonth()
        ];
      });
    });
  },
  methods: {
    FormatDay(day) {
      return moment(day).date();
    },
    FormatDate(day) {
      return moment(day).format("YYYY-MM-DD");
    }
  }
};
</script>

<style lang="less">
.el-calendar.schedule {
  .el-calendar__header {
    background-color: @ThemeColor;
    padding: 6.5px;

    .el-calendar__title {
      color: #ffffff;
    }

    .el-calendar__button-group {
      .el-button-group {
        .el-button {
          color: #409eff;
          background: #ecf5ff;
          border-color: #b3d8ff;

          span {
            cursor: pointer;
          }
        }
      }
    }
  }

  .el-calendar__body {
    padding: 5px 25px;

    .el-calendar-table {
      thead {
        th {
          padding: 5px 0;
          font-size: 14px;
          font-weight: 600;
        }
      }

      tbody {
        tr {
          td {
            border: none;
            background: none;
            text-align: center;
            vertical-align: middle;

            .el-calendar-day {
              height: auto;
              padding: 3px 0px;
              .dayItem {
                width: 32px;
                height: 32px;
                line-height: 32px;
                border-radius: 32px;
                cursor: pointer;
              }

              .el-badge {
                .el-badge__content {
                  background-color: @ThemeColor;
                  top: auto;
                  bottom: -8px;
                  right: 16px + 4px;
                }
              }
            }

            .el-calendar-day:hover {
              cursor: pointer;
              background: none;
            }
          }

          td.is-selected {
            .el-calendar-day {
              .dayItem {
                background-color: @ThemeColor;
                color: #ffffff;
              }

              .el-badge {
                .el-badge__content {
                  display: none;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>