<template>
  <div class="tbl" ref="div_Table">
    <div class="handle_bar" ref="HandleBar">
      <div class="search">
        <el-input
          class="likeSearch"
          :placeholder="
            SKey
              ? `请输入${SKeyName}模糊查询`
              : `选择${ModuleName}属性项搜索，或者输入关键字识别搜索`
          "
          v-model="SValue"
          maxlength="30"
          clearable
          show-word-limit
          @change="SearchValueChanged"
          @clear="Search"
          size="mini"
        >
          <el-button slot="append" icon="el-icon-search" @click="Search"></el-button>
        </el-input>
      </div>
      <div class="tools">
        <el-tooltip class="item" effect="light" content="刷新" placement="top">
          <el-button icon="el-icon-refresh" size="mini" type="info" plain @click="Search"></el-button>
        </el-tooltip>
        <el-button type="primary" size="mini" @click="CreateHandle">创建{{ ModuleName }}</el-button>
      </div>
    </div>
    <el-table
      v-loading="Loading"
      element-loading-text="数据正在加载中"
      ref="Table"
      :data="Datas"
      :fit="true"
      class="tblData"
      style="width: 100%"
      :max-height="500"
      @selection-change="SelectionChanged"
      @sort-change="SortChanged"
      @filter-change="FilterChanged"
      :row-key="KeyFileld"
    >
      <el-table-column
        class-name="sel_col"
        type="selection"
        width="35"
        v-if="ShowSelectColumn"
        fixed="left"
      ></el-table-column>
      <el-table-column
        :class-name="column.Code"
        :align="column.Align"
        :prop="column.Code"
        :column-key="column.Code"
        :label="column.Header"
        :width="column.Width"
        :min-width="column.MinWidth"
        :fixed="column.Fixed"
        :sortable="column.Sort"
        :filter-multiple="column.FilterMultiple === true"
        :filters="column.Filters"
        v-for="(column, index) in Columns"
        :show-overflow-tooltip="column.Code !== 'action_column'"
        :key="index"
      >
        <template slot-scope="scope">
          <span v-if="!column.Slot && !column.IsEstate">
            {{
            scope.row[column.Code]
            }}
          </span>
          <i
            v-if="column.IsEstate"
            class="estate"
            :class="{
              'el-icon-circle-check': parseInt(scope.row[column.Code]) === 1,
              'el-icon-remove': parseInt(scope.row[column.Code]) === 0
            }"
          >{{ parseInt(scope.row[column.Code]) === 1 ? "正常" : "停用" }}</i>
          <slot
            v-if="column.Slot && !column.IsEstate"
            :name="column.Code"
            :row="scope.row"
            :index="index"
            :value="scope.row[column.Code]"
          />
          <el-button-group v-if="column.Slot && column.ShowBaseActionBtn">
            <el-button
              v-if="column.NeedViewBtn"
              onfocus="this.blur()"
              type="primary"
              icon="el-icon-view"
              size="mini"
              plain
              @click="ViewHandle(scope.row)"
            >查看</el-button>
            <el-button
              :disabled="scope.row['EditDisabled']"
              onfocus="this.blur()"
              type="primary"
              icon="el-icon-edit"
              size="mini"
              plain
              @click="EditHandle(scope.row)"
            >修改</el-button>
            <slot name="ExAction" :row="scope.row" />
            <el-button
              v-if="!column.UnNeedEstateBtn && scope.row[EstateFiled] == 1"
              onfocus="this.blur()"
              plain
              size="mini"
              type="warning"
              icon="el-icon-remove-outline"
              @click="UpdateEstate(scope.row, 0)"
            >停用</el-button>
            <el-button
              onfocus="this.blur()"
              v-if="!column.UnNeedEstateBtn && scope.row[EstateFiled] != 1"
              plain
              type="success"
              icon="el-icon-circle-check"
              size="mini"
              @click="UpdateEstate(scope.row, 1)"
            >恢复</el-button>
            <el-button
              onfocus="this.blur()"
              size="mini"
              v-if="scope.row[EstateFiled] != -1"
              plain
              type="danger"
              icon="el-icon-close"
              @click="Delete(scope.row)"
            >删除</el-button>
          </el-button-group>
          <el-button-group v-if="column.Slot && column.ShowOrderBtn" class="order">
            <el-tooltip placement="top" content="移至最顶部">
              <el-button onfocus="this.blur()" icon="iconfont icon_move_top" size="mini" plain></el-button>
            </el-tooltip>
            <el-tooltip placement="top" content="上移">
              <el-button onfocus="this.blur()" icon="iconfont icon_move_up" size="mini" plain></el-button>
            </el-tooltip>
            <el-tooltip placement="bottom" content="下移">
              <el-button onfocus="this.blur()" icon="iconfont icon_move_up" size="mini" plain></el-button>
            </el-tooltip>
            <el-tooltip placement="bottom" content="移至最底部">
              <el-button onfocus="this.blur()" icon="iconfont icon_move_top" size="mini" plain></el-button>
            </el-tooltip>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <div class="el-table el-table--fit" ref="TblFoot">
      <div class="el-table__footer-wrapper">
        <table cellspacing="0" cellpadding="0" border="0" class="el-table__footer">
          <colgroup>
            <col width="40" v-if="ShowSelectColumn" />
            <col width="100%" />
          </colgroup>
          <tfoot>
            <tr class>
              <td v-if="ShowSelectColumn">
                <div class="cell">
                  <el-checkbox
                    v-model="CheckAll"
                    :indeterminate="isIndeterminate"
                    @change="CheckALLChanged"
                  ></el-checkbox>
                </div>
              </td>
              <td>
                <div class="cell">
                  <el-button-group>
                    <el-button
                      icon="el-icon-remove-outline"
                      size="mini"
                      :disabled="!isIndeterminate && !CheckAll"
                      @click="UpdateEstate(null, 0)"
                    >停用</el-button>
                    <el-button
                      icon="el-icon-circle-check"
                      size="mini"
                      :disabled="!isIndeterminate && !CheckAll"
                      @click="UpdateEstate(null, 1)"
                    >恢复</el-button>
                    <el-button
                      icon="el-icon-close"
                      size="mini"
                      :disabled="!isIndeterminate && !CheckAll"
                      @click="Delete()"
                    >删除</el-button>
                    <slot name="BatchAction" />
                  </el-button-group>
                  <el-pagination
                    :current-page.sync="PIndex"
                    :page-size.sync="PSize"
                    :total="TotalCounts"
                    @size-change="PageSizeChanged"
                    @current-change="PageChanged"
                    layout="total, sizes, prev, pager, next, jumper"
                  ></el-pagination>
                </div>
              </td>
            </tr>
          </tfoot>
        </table>
      </div>
    </div>
    <!-- 对话框 -->
    <el-dialog
      center
      custom-class="dlgModelBaseForm"
      :title="`${IsEdit ? '修改' : '创建'}${ModuleName}`"
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      append-to-body
      :modal-append-to-body="false"
      :close-on-press-escape="true"
      width="30%"
    >
      <el-form
        :disabled="SyncLoading"
        :model="BaseForm"
        :rules="BaseFormRule"
        ref="ModelBaseForm"
        :label-width="BaseForm.LabelWidth ? BaseForm.LabelWidth : '120px'"
        size="mini"
        status-icon
        @keyup.enter.native="ConfirmBaseForm"
      >
        <slot name="ModelBaseForm" />
      </el-form>
      <div slot="footer">
        <el-button-group>
          <el-button
            :loading="SyncLoading"
            type="primary"
            icon="el-icon-check"
            size="mini"
            @click="ConfirmBaseForm"
            autofocus
            native-type="submit"
          >确 定</el-button>
          <el-button icon="el-icon-close" size="mini" @click="dialogVisible = false">取 消</el-button>
        </el-button-group>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    // 主键字段
    KeyFileld: {
      type: String,
      default: ""
    },
    // 状态字段
    EstateFiled: {
      type: String,
      default: ""
    },
    // 显示导出按钮
    Loading: {
      type: Boolean,
      default: false
    },
    // 模块名称
    ModuleName: {
      type: String,
      default: ""
    },
    Datas: {
      type: Array,
      default: () => []
    },
    // 显示选择列
    ShowSelectColumn: {
      type: Boolean,
      default: true
    },
    // 列
    Columns: {
      type: Array,
      default: () => []
    },
    PageIndex: {
      type: Number,
      default: 1
    },
    // 分页大小
    PageSize: {
      type: Number,
      default: 20
    },
    // 总行数
    TotalCounts: {
      type: Number,
      default: 0
    },
    // 排序字段
    SortField: {
      type: String,
      default: ""
    },
    // 排序方向
    SortDir: {
      type: Number,
      default: 0
    },
    // 基础表单
    BaseForm: {
      type: Object,
      default: null
    },
    // 基础表单规则
    BaseFormRule: {
      type: Object,
      default: null
    }
  },
  watch: {
    $route: function(newRouter, oldRouter) {
      this.$emit("update:PageIndex", 1);
      this.$emit("LoadData");
    },
    PageIndex(newVal, oldVal) {
      this.PIndex = newVal;
    }
  },
  data() {
    return {
      msgg: "",
      dialogVisible: false,
      SKey: this.SearchKey,
      SKeyName: "",
      SValue: this.SearchValue,
      CheckAll: false,
      isIndeterminate: false,
      PIndex: this.PageIndex,
      PSize: this.PageSize,
      Selections: [],
      IsEdit: false,
      SyncLoading: false
    };
  },
  mounted() {},
  methods: {
    SearchKeyChanged() {
      const find = this.SearchKeyList.find(item => item.Code === this.SKey);
      if (find) this.SKeyName = find.Name;
      this.$emit("update:SearchKey", this.SKey);
    },
    SearchValueChanged() {
      this.$emit("update:SearchValue", this.SValue);
    },
    /**
     * 当选择项发生变化时会触发该事件
     */
    SelectionChanged(val) {
      if (val && val.length > 0) {
        if (val.length === this.Datas.length) {
          this.CheckAll = true;
          this.isIndeterminate = false;
        } else {
          this.CheckAll = false;
          this.isIndeterminate = true;
        }
      } else {
        this.CheckAll = false;
        this.isIndeterminate = false;
      }

      this.$emit("update:selections", val);
      this.Selections = val;
    },
    /**
     * 底部的复选框切换
     */
    CheckALLChanged() {
      this.$refs.Table.toggleAllSelection();
    },
    /**
     * 当表格的排序条件发生变化的时候会触发该事件
     */
    SortChanged(orderInfo) {
      if (orderInfo) {
        this.$emit("update:SortField", orderInfo.prop);
        this.$emit("update:SortDir", orderInfo.order === "descending" ? 1 : 0);
        this.$emit("LoadData");
      }
    },
    /**
     * 当表格的筛选条件发生变化的时候会触发该事件，参数的值是一个对象，对象的 key 是 column 的 columnKey，对应的 value 为用户选择的筛选条件的数组。
     */
    async FilterChanged(filters) {
      try {
        const key = Object.keys(filters)[0];
        // 动态同步父组件的筛选值。
        const column = this.Columns.find(c => c.Code === key);

        let value = filters[key][0];

        if (column && column.FilterMultiple) {
          value = "";
          await filters[key].forEach((f, i) => {
            value += f;
            if (i < filters[key].length - 1) {
              value += ",";
            }
          });
        }

        this.$emit(`update:${key}`, value);
        this.$emit("LoadData");
      } catch {}
    },
    /**
     * 搜索
     */
    Search() {
      this.$emit("update:PageIndex", 1);
      this.$emit("LoadData");
    },
    /**
     * 刷新直接重新加载数据
     */
    Refresh() {
      this.$emit("LoadData");
    },
    /**
     * 分页大小改变时会触发
     */
    PageSizeChanged(val) {
      this.$emit("update:PageIndex", 1);
      this.$emit("update:PageSize", val);
      this.$emit("LoadData");
    },
    /**
     * 切换分页时触发
     * @param {number} val 新的页码
     */
    PageChanged(val) {
      this.$emit("update:PageIndex", val);
      this.$emit("update:PageSize", this.PageSize);
      this.$emit("LoadData");
    },
    /**
     * 更新状态
     */
    async UpdateEstate(row, estate) {
      if (row) {
        this.$emit("UpdateEstate", row[this.KeyFileld], estate);
      } else {
        if (this.Selections.length > 0) {
          let ids = "";
          await this.Selections.forEach(row => {
            ids += row[this.KeyFileld] + ",";
          });

          this.$emit("UpdateEstate", ids, estate);
        }
      }
    },
    /**
     * 删除
     */
    Delete(row) {
      this.$confirm("此操作将永久删除这些数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(async () => {
          if (row) {
            this.$emit("Delete", row[this.KeyFileld]);
          } else {
            if (this.Selections.length > 0) {
              let ids = "";
              await this.Selections.forEach(row => {
                ids += row[this.KeyFileld] + ",";
              });

              this.$emit("Delete", ids);
            }
          }
        })
        .catch(() => {});
    },
    /**
     * 创建按钮点击
     */
    CreateHandle() {
      this.IsEdit = false;
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.$emit("CreateHandle");
      });
    },
    /**
     * 编辑行按钮点击
     */
    EditHandle(row) {
      this.IsEdit = true;
      this.dialogVisible = true;
      this.$emit("EditRowHandle", row);
    },
    /**
     * 查看行按钮点击
     */
    ViewHandle(row) {
      this.$emit("ViewRowHandle", row);
    },
    /**
     * 基础表单提交
     */
    ConfirmBaseForm() {
      this.SyncLoading = true;
      this.$refs.ModelBaseForm.validate(async valid => {
        if (valid) {
          await this.$emit(
            "ConfirmBaseForm",
            () => {
              this.dialogVisible = false;
              this.$emit("LoadModelData");
              this.SyncLoading = false;
            },
            () => {
              this.SyncLoading = false;
            }
          );
        } else {
          this.SyncLoading = false;
        }
      });
    }
  }
};
</script>

<style lang="less">
.tbl {
  @MarginTop: 16px;

  margin-top: @MarginTop;
  height: calc(100% - @MarginTop * 2) !important;

  .handle_bar {
    display: grid;
    grid-template-columns: 1fr auto;

    .search {
      display: inline-block;

      .likeSearch {
        width: 515px;
      }
      .likeSearch.el-input-group--prepend {
        width: 616px;
      }

      .el-select {
        .el-input__inner {
          width: 100px;
        }

        .el-select-dropdown__item.selected {
          font-weight: 500;
        }
      }

      // .el-input-group {
      //   > .el-input__inner {
      //     width: 460px;
      //   }
      // }

      .el-button.is-disabled,
      .el-button.is-disabled:focus,
      .el-button.is-disabled:hover {
        border-color: transparent;
        background-color: transparent;
      }
    }

    .tools {
      .el-button {
        i {
          font-size: 13px;
        }
      }

      .el-button {
        margin-left: 3px;
      }

      .el-button--text + .el-button {
        margin-left: 10px;
      }

      .el-button--info.is-plain {
        background-color: #f7f7f7;
      }

      .el-button--info.is-plain:focus,
      .el-button--info.is-plain:hover {
        background: #fff;
        border-color: #ddd;
        color: #333;
      }
    }
  }

  .breadcrumb {
    display: inline-block;

    .el-breadcrumb {
      border-left: 2px solid #88b7e0;
      color: #999;
      margin: 5px 10px 0px 0px;
      font-size: 12px;
      padding-left: 8px;

      .el-breadcrumb__inner a,
      .el-breadcrumb__inner.is-link {
        font-weight: 500;
      }
    }
  }

  .dlgModelBaseForm {
    min-width: 300px;
  }
}

.el-table {
  font-size: 12px;
  margin-top: 3px;
  border: 1px solid #e1e6eb;

  .el-table__header {
    thead {
      tr {
        th {
          padding: 2px 5px;
          font-weight: normal;
          color: #999;
          border-bottom: 1px solid #e1e6eb;
          background-color: #f5f6fa;

          .cell {
            padding-left: 5px;
            padding-right: 5px;
          }
        }
      }
    }
  }

  .el-table__footer {
    tfoot {
      tr {
        td {
          padding: 5px;
          border-bottom: none;
        }
      }
    }
  }

  .el-table__body {
    tbody {
      tr {
        td {
          padding: 5px;
          border-top: 0px;
          color: @FontColor;
          border-bottom: 1px solid #e1e6eb;
          vertical-align: middle;

          .estate {
            font-size: 12px;
          }

          .estate:before {
            margin-right: 3px;
          }

          .estate.el-icon-circle-check {
            color: #67c23a;
          }

          .estate.el-icon-remove {
            color: #f56c6c;
          }
        }

        // 操作列公共处理
        .action_column {
          .cell {
            text-overflow: clip;
          }

          .el-button-group + .el-button-group {
            margin-left: 5px;
          }

          .el-button-group.order {
            .el-button:nth-of-type(n + 3) {
              .transform(180deg);
            }
          }
        }
      }

      tr:last-child {
        td {
          border-bottom: 0px;
        }
      }
    }
  }

  .cell {
    padding-left: 5px;
    padding-right: 5px;

    .iconfont {
      font-size: 12px;
    }
  }
}

.el-table + .el-table {
  margin-top: 0px;
  border-top: none;
}

.el-table__fixed-right::before,
.el-table__fixed::before {
  content: none;
}

.el-table__fixed-right-patch {
  background-color: #f5f6fa;
}

.el-table--border::after,
.el-table--group::after,
.el-table::before {
  height: 0px;
  background-color: transparent;
}

.el-pagination {
  @FontColor: #888;

  float: right;

  button,
  .el-pager li {
    font-size: 12px;
    color: #999999;
    background-color: #ffffff;
    border: 1px solid #dddddd;
  }

  button:not(:disabled):hover,
  .el-pager li:hover {
    color: #fff;
    background-color: fade(@ThemeColor, 60%);
    border-color: fade(@ThemeColor, 60%);
  }

  button:disabled {
    color: #bbb;
    border: 1px solid #ddd;
    background-color: #f7f7f7;
    i {
      cursor: not-allowed;
    }
  }

  .el-pager {
    li:first-child {
      border-left: none;
    }

    li + li {
      border-right: none;
    }

    li.active {
      color: #ffffff;
      cursor: default;
      background-color: @ThemeColor;
      border: 1px solid @ThemeColor;
    }
  }

  .btn-prev,
  .btn-next {
    i {
      display: block;
    }
  }

  .btn-prev {
    padding-right: 6px;
  }

  .btn-next {
    padding-left: 6px;
  }

  button,
  span:not([class*="suffix"]) {
    font-size: 12px;
  }

  .el-pagination__jump {
    .el-input {
      margin-left: 5px;
      margin-right: 5px;
      font-size: 12px;
    }
  }

  .el-pagination__total {
    color: @FontColor;
  }

  .el-pagination__sizes {
    .el-input {
      .el-input__inner {
        font-size: 12px;
        padding-right: 22px;
        color: @FontColor;
      }
    }
  }
}
</style>
