<template lang="html">
  <div class="s-tbl" id="lsTable">
    <div class="content">
      <el-table
        v-loading
        element-loading-text="数据正在加载中"
        ref="Table"
        :data="Datas"
        :row-style="rowStyle"
        :fit="true"
        :max-height="MaxTblHeight"
        :highlight-current-row="isCurrentRow"
        @selection-change="SelectionChanged"
        @current-change="CurrentChange"
        @row-click="rowClick"
      >
        <el-table-column
          class-name="sel_col"
          type="selection"
          v-if="showSelectColumn"
          width="35"
          fixed="left"
        ></el-table-column>

        <template v-for="(column, index) in Columns">
          <el-table-column
            v-if="column.Slot" :key="index" :label="column.Header" :class-name="column.ClassName"
            :fixed="column.Fixed" :align="column.Align" :width="column.Width" :min-width="column.MinWidth"
          >
            <template slot-scope="scope">
              <slot :name="column.Slot" :row="scope.row"/>
            </template>
          </el-table-column>

          <el-table-column
            v-else  :key="index" :prop="column.Code" :label="column.Header" :class-name="column.ClassName"
            :fixed="column.Fixed" :align="column.Align" :width="column.Width" :min-width="column.MinWidth"
          />
        </template>
      </el-table>
    </div>
    <div class="ctls" :style="{height: ctlHeight}" v-if="needCtl">
      <slot name="ctlHeader"/>
      <div class="ctl-con" :style="{height: ctlConHeight}">
        <slot name="ctlList"/>
      </div>
    </div>
    <div class="logs" :style="{height: ctlHeight}" v-if="needLog">
      <slot name="ctlHeader"/>
      <div class="log-con">
        <slot name="logList"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    Datas: {
      type: Array,
      default: () => []
    },
    Columns: {
      type: Array,
      default: () => []
    },
    SOutHeight: {
      type: Number
    },
    /**
     * 多选行数据
     */
    selections: {
      type: Array,
      default: () => []
    },
    /**
     * 单选行数据
     */
    selection: {
      type: Object,
      default: null
    },
    needCtl: {
      type: Boolean,
      default: true
    },
    needLog: {
      type: Boolean,
      default: false
    },
    /**
     * 显示选择列、多选
     */
    showSelectColumn: {
      type: Boolean,
      default: true
    },
    /**
     * 是否单选
     */
    isCurrentRow: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      onceFlag: true,
      selFlag: 0,
      MaxTblHeight: null,
      ctlHeight: null,
      ctlConHeight: null
    };
  },
  watch: {
    SOutHeight() {
      this.AdjustHeight();
    }
  },
  mounted() {
    addEventListener("keydown", this.keyDown, false);
    addEventListener("keyup", this.keyUp, false);
  },
  updated() {
    if (this.SOutHeight && this.onceFlag) {
      this.AdjustHeight();

      this.onceFlag = false;
    }
  },
  beforeDestroy() {
    removeEventListener("keydown", this.keyDown);
    removeEventListener("keyup", this.keyUp);
  },
  computed: {
    Selections: {
      get() {
        return this.selections;
      },
      set(val) {
        this.$emit("update:selections", val);
      }
    },
    Selection: {
      get() {
        return this.selection;
      },
      set(val) {
        this.$emit("update:selection", val);
      }
    }
  },
  methods: {
    /**
     * 高度调整
     */
    AdjustHeight() {
      const ctlHeader = document.querySelector(".ctls .header");
      const lsTable = document.getElementById("lsTable");

      const tblHeight = window.getComputedStyle(lsTable).height;

      const ctlHeaderH = ctlHeader
        ? window.getComputedStyle(ctlHeader).height
        : 0;

      this.MaxTblHeight =
        parseInt(tblHeight) - parseInt(this.SOutHeight) - 16 - 3;

      this.ctlHeight =
        parseInt(tblHeight) - parseInt(this.SOutHeight) - 3 - 16 + "px";
      this.ctlConHeight =
        parseInt(tblHeight) -
        parseInt(this.SOutHeight) -
        16 -
        3 -
        2 -
        parseInt(ctlHeaderH) +
        "px";
    },
    /**
     * 当选择项发生变化时会触发该事件
     */
    SelectionChanged(row) {
      this.Selections = row;
    },
    /**
     * 点击行触发，选中或不选中复选框
     */
    rowClick(row, column, event) {
      const refsElTable = this.$refs.Table; // 获取表格对象
      if (this.CtrlDown) {
        refsElTable.toggleRowSelection(row); // ctrl多选 如果点击两次同样会取消选中
        return;
      }

      refsElTable.toggleRowSelection(row); // 调用选中行方法
    },

    CurrentChange(row) {
      this.Selection = row;
    },

    rowStyle({ row, rowIndex }) {
      Object.defineProperty(row, "rowIndex", {
        // 给每一行添加不可枚举属性rowIndex来标识当前行
        value: rowIndex,
        writable: true,
        enumerable: false
      });
    },

    keyDown(event) {
      if (event.ctrlKey) this.CtrlDown = true;
    },

    keyUp(event) {
      if (event.ctrlKey) this.CtrlDown = false;
    }
  }
};
</script>

<style lang="less">
@TextGray: #b2b2b2;
@bcolor: #e1e6eb;
@offgray: #e8e8e8;
ul li {
  list-style: none;
}
.s-tbl {
  width: 100% !important;
  // height: 100%;
  box-sizing: border-box;
  height: calc(100% - 16px);
  overflow: hidden;
  .content {
    width: 76%;
    float: left;
    overflow: hidden;
  }
}
.el-table {
  font-size: 12px;
  margin-top: 3px;
  border-left: 1px solid #e1e6eb;
  border-right: 1px solid #e1e6eb;
  box-sizing: border-box;
  &::before {
    background: #e1e6eb;
    top: 0;
    z-index: 5;
  }
  &::after {
    height: 1px;
    width: 100%;
    content: "";
    background: #e1e6eb;
    position: absolute;
    bottom: 0;
    z-index: 111;
  }
  tr:last-child td,
  tr:last-child th {
    border-bottom: 0;
  }

  .el-table__header {
    thead {
      tr {
        th {
          padding: 0 5px;
          line-height: 36px;
          font-weight: normal;
          color: #999;
          border-bottom: 1px solid #e1e6eb;
          background-color: #f5f6fa;

          .cell {
            padding-left: 5px;
            padding-right: 5px;
          }
        }
      }
    }
  }
  .ls_info_col {
    span {
      display: block;
      line-height: 24px;
      color: @TextGray;
      font-weight: 600;
      &.tip {
        line-height: 28px;
        font-weight: 400;
        padding-top: 5px;
      }
    }
  }
  .gw_state_col {
    .online {
      color: @ThemeColor;
    }
    .offline {
      color: @RedColor;
    }
  }

  .panel {
    display: inline-flex;
    flex-direction: column;
    text-align: center;
    > div {
      display: block;
      width: 48px;
      height: 48px;
      line-height: 46px;
      border: 1px solid @GreenColor;
      box-sizing: border-box;
      margin: 5px 5px 0;

      .iconfont {
        font-size: 12px;
        margin: 0 1px;
      }
    }
    > span {
      display: inline-block;
      line-height: 28px;
      color: @TextGray;
    }
    &.offline {
      .iconfont {
        color: @offgray;
      }
      > div {
        border: 1px solid @offgray;
      }
      > span {
        color: @offgray;
      }
    }
  }
}
.ctls {
  float: right;
  width: calc(24% - 10px);
  margin: 3px 0 16px 10px;
  border: 1px solid @bcolor;
  font-size: 12px;
  overflow: hidden;
  box-sizing: border-box;
  .ctl-con {
    overflow-y: auto;
    overflow-x: hidden;
  }

  .header {
    height: 40px;
    line-height: 40px;
    padding: 0 10px 0 20px;
    border-bottom: 1px solid @bcolor;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .title {
      font-size: 14px;
    }
  }
  .list {
    margin-top: 8px;
    .name {
      color: #666;
      font-weight: 600;
      line-height: 24px;
      padding-left: 26px;
    }
    ul {
      width: 100%;
      margin-top: 6px;
      margin-left: 2%;
      margin-right: 2%;
      overflow: hidden;
    }
    li {
      margin: 10px 4%;
      float: left;
      width: 40%;
      height: 0;
      padding-bottom: 40%;
      position: relative;
      overflow: hidden;
      border: 1px solid @GreenColor;
      color: @GreenColor;
      text-align: center;

      > div {
        position: absolute;
        width: 100%;
        height: 100%;

        h3 {
          font-size: 12px;
          font-weight: normal;
          text-align: left;
          padding-left: 10px;
          line-height: 24px;
        }

        .sws {
          position: absolute;
          top: 50%;
          margin-top: -16px;
          width: 100%;
          height: 32px;
          line-height: 32px;
          padding: 0 2%;
          box-sizing: border-box;
          display: flex;

          .iconfont {
            flex: 1;
            text-align: center;
            font-size: 32px;
            cursor: pointer;
          }
        }
      }
      &.offline {
        border: 1px solid @offgray;
        h3,
        .out .iconfont {
          color: @offgray;
        }
        .sws .iconfont {
          cursor: not-allowed;
        }
      }
    }
  }
}
.logs {
  float: right;
  width: calc(24% - 10px);
  margin: 3px 0 16px 10px;
  border: 1px solid @bcolor;
  font-size: 12px;
  overflow: hidden;
  box-sizing: border-box;
  .ctl-con {
    overflow-y: auto;
    overflow-x: hidden;
  }
  .header {
    height: 40px;
    line-height: 40px;
    padding: 0 10px 0 20px;
    border-bottom: 1px solid @bcolor;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .title {
      font-size: 14px;
    }
  }
}
@media (min-width: 1000px) and (max-width: 1600px) {
  .s-tbl .content {
    width: 72%;
  }
  .ctls {
    width: calc(28% - 10px);
  }
}
</style>
