<template>
  <div id="class-screen">

    <div class="content">
      <div class="head">

        <div class="mb">
          <div class="mb_title">
            <img :src="getPicture(cpdata.SSContent)" alt/>
          </div>
          <div class="mb">
            答案：
            <span class="primary">{{cpdata.SSAnswer || '无标注答案'}}</span>
          </div>
        </div>

        <div class="part_title">我的答题</div>
        <div class="part_line">
          <div v-if="cpdata['SSType'] + '' === '4'" v-html="saContent"></div>
          <div v-else>{{saContent || '未作答'}}</div>
        </div>

        <div class="part_title">问卷调研</div>
        <div class="part_line">{{saMaster || '未参与'}}</div>

      </div>
    </div>
  </div>
</template>

<script>
import api from "@/scripts/api";
import store from "@/scripts/store";
import { ModulePicture } from "@/scripts/dictionary";

export default {
  props: {
    cpdata: {
      type: Object,
      default: () => {
        return {};
      }
    },
    subjectId: {
      type: String,
      default: ""
    }
  },
  data () {
    return {
      dataLoading: false,
      subjectType: "",
      saContent: "",
      saMaster: ""
    };
  },
  created () {
    this.getStudentAnswer();
  },
  methods: {
    getStudentAnswer () {
      const studentInfo = store.GetStudentInfo();
      this.dataLoading = true;
      api.IScreenSubject.StudentAnswerInfo(
        this.subjectId,
        studentInfo ? studentInfo.StudentID : ""
      ).then(res => {
        if (res.Result && res.Data) {
          const htmlstr = res.Data.SAContent;
          this.saContent = htmlstr.replace(/<img [^>]*src=['"]([^'"]+)[^>]*>/gi, (match, capture) => {
            const filename = capture.split("/")[1];
            return `<img src="${this.$root.PictureURL}?filename=${filename}&fileType=${ModulePicture.ScreenAnswer}&platFormType=1" alt="" style="max-width:380px"/>`;
          });
          this.saMaster = res.Data.SAMaster === 0 ? "未掌握" : "已掌握";
        }
        this.dataLoading = false;
      }).catch(() => {
        this.dataLoading = false;
      });
    },

    getPicture (val) {
      return `${this.$root.PictureURL}?filename=${val}&fileType=${ModulePicture.ScreenSubject}&platFormType=1`;
    }
  }
};
</script>

